

























export default {
	data() {
		return {
			region: [],
			regionName: '',
			isDefault: true
		};
	},
	methods: {
		RegionChange(e) {
			this.region = e.detail.value;
			this.regionName = this.region.join(',')
		},
		changeDefault(e) {
			this.isDefault = e.detail.value;
		}
	}
}
